'use client';

import React from 'react';
import ExportedImage from 'next-image-export-optimizer';

type Props = {
  mockRef: React.RefObject<HTMLDivElement | null>;
};

const FeatureItemVert: React.FC<{
  icon?: string;
  title: string;
  text: string;
  side?: 'left' | 'right';
  style?: React.CSSProperties;
}> = ({ icon, title, text, side = 'left', style }) => {
  const isLeft = side === 'left';

  return (
    <div
      className={`w-[220px] flex flex-col ${
        isLeft ? 'items-start text-left' : 'items-end text-right'
      }`}
      style={style}
      role="group"
    >
      {icon && (
        <div className={`${isLeft ? 'self-start' : 'self-end'} mb-2`}>
          <div className="w-[60px] h-[60px] flex items-center justify-center">
            <ExportedImage
              src={icon}
              alt={title}
              width={60}
              height={60}
              className="w-[60px] h-[60px] object-contain block"
              style={{ backgroundColor: 'transparent' }}
            />
          </div>
        </div>
      )}

      <div className="text-[20px] font-bold leading-tight">{title}</div>
      <div className="text-[12px] font-normal text-gray-600 mt-1 leading-snug max-w-[220px]">
        {text}
      </div>
    </div>
  );
};

const FeatureCircleSection: React.FC<Props> = ({ mockRef }) => {
  return (
    <section
      id="features"
      className="relative py-20 lg:py-28 overflow-visible bg-[var(--bg-page,#fbf9fb)]"
    >
      {/* SVG rings background */}
      <div className="absolute inset-0 pointer-events-none z-0 flex items-center justify-center">
        <svg
          viewBox="0 0 1200 1000"
          className="w-[95%] h-[95%] max-w-[1400px] max-h-[1100px] object-contain"
          preserveAspectRatio="xMidYMid meet"
          aria-hidden
        >
          <defs>
            <linearGradient id="ringsGradient" x1="0" x2="1" y1="0" y2="1">
              <stop offset="0%" stopColor="#0D70DF" />
              <stop offset="100%" stopColor="#E63637" />
            </linearGradient>
          </defs>
          <g transform="translate(600,520)">
            <circle
              r="260"
              fill="none"
              stroke="url(#ringsGradient)"
              strokeWidth="5"
              strokeDasharray="12 16"
              strokeLinecap="round"
              strokeOpacity="0.4"
            />
            <circle
              r="190"
              fill="none"
              stroke="url(#ringsGradient)"
              strokeWidth="4.5"
              strokeDasharray="10 14"
              strokeLinecap="round"
              strokeOpacity="0.26"
            />
            <circle
              r="120"
              fill="none"
              stroke="url(#ringsGradient)"
              strokeWidth="3.5"
              strokeDasharray="8 12"
              strokeLinecap="round"
              strokeOpacity="0.14"
            />
          </g>
        </svg>
      </div>

      <div className="container mx-auto px-4 lg:px-8 relative z-10">
        {/* Надзаголовок с иконкой */}
        <div className="flex items-center justify-center mb-6">
          <span className="inline-block py-1 px-3 bg-primary-red/10 text-primary-red rounded-full text-sm font-medium mb-4">
            <i className="fa-solid fa-gear mr-1" /> Совершенствуйте показатели по всем направлениям
          </span>
        </div>

        {/* Заголовок на 2 строки */}
        <div className="text-center mb-6">
          <h2 className="text-3xl sm:text-4xl md:text-5xl font-bounded font-bold leading-tight tracking-tight">
            <span>Надёжное решение</span>
            <span className="block">для детских лагерей</span>
          </h2>
        </div>

        <div className="relative flex items-center justify-center min-h-[820px] lg:min-h-[1040px]">
          {/* Мокап */}
          <div className="relative z-20 flex items-center justify-center">
            <div
              ref={mockRef}
              className="w-[320px] sm:w-[380px] md:w-[460px] lg:w-[560px] h-auto rounded-3xl pointer-events-none"
              style={{ height: 860, visibility: 'hidden' }}
            />
            <noscript>
              <div className="w-[320px] sm:w-[380px] md:w-[460px] lg:w-[560px] mx-auto">
                <img
                  src="/images/hero-tablet.png"
                  alt="Интерфейс системы (статичный)"
                  style={{ width: '100%', height: 'auto', borderRadius: 20 }}
                />
              </div>
            </noscript>
          </div>

          {/* Левая колонка */}
          <div className="hidden lg:block absolute left-0 top-0 bottom-0 w-[260px]">
            <div className="relative h-full">
              <div className="absolute" style={{ top: '16%', left: '70px' }}>
                <FeatureItemVert
                  icon="/images/icons/24h.png"
                  title="24/7"
                  text="Система автономно продаёт и оформляет путёвки без участия сотрудников"
                  side="left"
                />
              </div>
              <div className="absolute" style={{ top: '50%', left: '56px' }}>
                <FeatureItemVert
                  icon="/images/icons/5min.png"
                  title="5 минут"
                  text="Среднее время, которое родитель тратит на оформление и оплату путёвки"
                  side="left"
                />
              </div>
              <div className="absolute" style={{ top: '84%', left: '70px' }}>
                <FeatureItemVert
                  icon="/images/icons/70pc.png"
                  title="На 70%"
                  text="Сокращается время на формирование документооборота смены"
                  side="left"
                />
              </div>
            </div>
          </div>

          {/* Правая колонка */}
          <div className="hidden lg:block absolute right-0 top-0 bottom-0 w-[260px]">
            <div className="relative h-full">
              <div className="absolute" style={{ top: '16%', right: '70px' }}>
                <FeatureItemVert
                  icon="/images/icons/40pc.png"
                  title="До 40%"
                  text="Увеличение эффективности типовых процессов лагеря"
                  side="right"
                />
              </div>
              <div className="absolute" style={{ top: '50%', right: '56px' }}>
                <FeatureItemVert
                  icon="/images/icons/50pc.png"
                  title="До 50%"
                  text="Экономии на комиссиях при приёме оплаты за путёвки"
                  side="right"
                />
              </div>
              <div className="absolute" style={{ top: '84%', right: '70px' }}>
                <FeatureItemVert
                  icon="/images/icons/100pc.png"
                  title="На 100%"
                  text="Улучшенная система аналитики"
                  side="right"
                />
              </div>
            </div>
          </div>

          {/* Мобильная сетка */}
          <div className="lg:hidden mt-10 w-full">
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-6 justify-items-center">
              <FeatureItemVert icon="/images/icons/24h.png" title="24/7" text="Система автономно продаёт и оформляет путёвки без участия сотрудников" />
              <FeatureItemVert icon="/images/icons/40pc.png" title="До 40%" text="Увеличение эффективности типовых процессов лагеря" />
              <FeatureItemVert icon="/images/icons/5min.png" title="5 минут" text="Среднее время, которое родитель тратит на оформление и оплату путёвки" />
              <FeatureItemVert icon="/images/icons/50pc.png" title="До 50%" text="Экономии на комиссиях при приёме оплаты за путёвки" />
              <FeatureItemVert icon="/images/icons/70pc.png" title="На 70%" text="Сокращается время на формирование документооборота смены" />
              <FeatureItemVert icon="/images/icons/100pc.png" title="На 100%" text="Улучшенная система аналитики" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default FeatureCircleSection;
